<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar flat color="white">
        <v-btn>
          <v-icon @click="crearPDF()">print</v-icon>
        </v-btn>
        <v-toolbar-title>Artículos</v-toolbar-title>

        <v-divider class="mx-2" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-text-field
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Búsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>

        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">Nuevo</v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs6 sm6 md6>
                    <v-text-field v-model="code" label="Codigo"></v-text-field>
                  </v-flex>
                  <v-flex xs6 sm6 md6>
                    <v-text-field v-model="name" label="Name"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm12 md12>
                    <v-select
                      v-model="categoria"
                      :items="categorias"
                      label="Categoría"
                    >
                    </v-select>
                  </v-flex>
                  <v-flex xs6 sm6 md6>
                    <v-text-field
                      v-model="sale_price"
                      label="Precio"
                    ></v-text-field>
                  </v-flex>
                  <v-flex xs6 sm6 md6>
                    <v-text-field
                      v-model="stock"
                      type="number"
                      label="Stock"
                    ></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm12 md12>
                    <v-text-field
                      v-model="description"
                      label="Description"
                    ></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm12 md12 v-if="valida">
                    <div
                      class="red--text"
                      v-for="v in validaMensaje"
                      :key="v"
                      v-text="v"
                    ></div>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="close">Cancelar</v-btn>
              <v-btn color="blue darken-1" flat @click="guardar">Guardar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <v-dialog v-model="addModal" max-width="500px">
          <v-card>
            <v-card-title v-if="addAction == 2">
              <span class="headline">Activar Item</span>
            </v-card-title>
            <v-card-title v-if="addAction == 1">
              <span class="headline">Desactivar Item</span>
            </v-card-title>

            <v-card-text>
              vas a <span v-if="addAction == 1"> activar</span
              ><span v-if="addAction == 2"> desactivar</span> el articulo
              <span v-if="addAction == 1" class="green--text">
                {{ addName }}</span
              >
              <span v-if="addAction == 2" class="red--text">
                {{ addName }}</span
              >
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="addModal = 0"
                >Cancelar</v-btn
              >
              <v-btn
                color="green darken-1"
                flat
                @click="activar()"
                v-if="addAction == 1"
                >Activar</v-btn
              >
              <v-btn
                color="red darken-1 "
                flat
                @click="desactivar()"
                v-if="addAction == 2"
                >Desactivar</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
      <v-data-table
        :headers="headers"
        :items="articulos"
        :search="search"
        class="elevation-1"
      >
        <template v-slot:items="props">
          <td>{{ props.item.code }}</td>
          <td>{{ props.item.name }}</td>
          <td>{{ props.item.category.name }}</td>
          <td>{{ props.item.stock }}</td>
          <td>{{ props.item.sale_price }}</td>
          <td>{{ props.item.description }}</td>
          <td>
            <div v-if="props.item.status">
              <span class="blue--text">Activo</span>
            </div>
            <div v-else>
              <span class="red--text">Inactivo</span>
            </div>
          </td>
          <td class="justify-center layout px-0">
            <v-icon small class="mr-2" @click="editItem(props.item)">
              edit
            </v-icon>
            <template v-if="props.item.status">
              <v-icon
                medium
                color="red"
                @click="estadoCategoria(2, props.item)"
              >
                block
              </v-icon>
            </template>
            <template v-else>
              <v-icon
                medium
                color="green"
                @click="estadoCategoria(1, props.item)"
              >
                check
              </v-icon>
            </template>
          </td>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary" @click="listar()">Reset</v-btn>
        </template>
      </v-data-table>
    </v-flex>
  </v-layout>
</template>
<script>
import axios from "axios";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";

export default {
  data() {
    return {
      dialog: false,
      search: "",
      articulos: [],
      headers: [
        {
          text: "Código",
          align: "left",
          sortable: true,
          value: "code",
        },
        { text: "Nombre", value: "name", sortable: true },
        { text: "Categoría", value: "category.name", sortable: true },
        { text: "Stock", value: "stock", sortable: true },
        { text: "Precio", value: "precio", sortable: false },
        { text: "Descripcion", value: "descripcion", sortable: false },
        { text: "Estado", value: "estado", sortable: false },
        { text: "Acciones", value: "name", sortable: false },
      ],
      editedIndex: -1,
      _id: "",
      code: "",
      name: "",
      categoria: "",
      categorias: [],
      stock: 0,
      sale_price: 0,
      description: "",
      valida: 0,
      validaMensaje: [],
      addModal: 0,
      addAction: 0,
      addName: "",
      add_id: "",
    };
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nuevo Registro" : "Editar Registro";
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
  },
  created() {
    //this.initialize();
    this.listar();
    this.selectCategoria();
  },
  methods: {
    crearPDF() {
      var columns = [
        { title: "Nombre", dataKey: "nombre" },
        { title: "Código", dataKey: "codigo" },
        { title: "Categoría", dataKey: "categoria" },
        { title: "Stock", dataKey: "stock" },
        { title: "Precio Venta", dataKey: "precio_venta" },
      ];
      var rows = [];

      this.articulos.map(function(x) {
        rows.push({
          nombre: x.name,
          codigo: x.codigo,
          categoria: x.category.name,
          stock: x.stock,
          precio_venta: x.sale_price,
        });
      });
      var doc = new jsPDF("p", "pt");
      doc.autoTable(columns, rows, {
        margin: { top: 60 },
        didDrawPage: function(data) {
          doc.text("Lista de Artículos", 40, 30);
        },
      });

      doc.save("Articulos.pdf");
    },
    selectCategoria() {
      let me = this;
      let categoriaArray = [];
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .get("category/list", config)
        .then((response) => {
          categoriaArray = response.data;
          categoriaArray.map(function(x) {
            me.categorias.push({ text: x.name, value: x._id });
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },
    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .get("item/list", config)
        .then((response) => {
          me.articulos = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    limpiar() {
      this._id = "";
      this.name = "";
      this.description = "";
      this.valida = 0;
      this.validaMensaje = [];
      this.editedIndex = -1;
      this.stock = 0;
      this.sale_price = "";
      this.categoria = 0;
    },
    valdiar() {
      this.valida = 0;
      this.validaMensaje = [];
      if (!this.categoria) {
        this.validaMensaje.push("Selecione una categoria.");
      }
      if (this.code.length > 50) {
        this.validaMensaje.push("El codigo tiene un maximo de 50 caracteres.");
      }
      if (this.code.length < 1 || this.name.length > 50) {
        this.validaMensaje.push(
          "El nombre debe de ser entre 1 a 50 caracteres."
        );
      }
      if (this.description.length > 255) {
        this.validaMensaje.push(
          "El descripcion debe de ser menor a 255 caracteres."
        );
      }
      if (!this.stock || this.stock < 0) {
        this.validaMensaje.push("Ingrese un stock valido");
      }
      if (!this.sale_price) {
        this.validaMensaje.push("Ingrese un precio valido.");
      }
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    guardar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      if (this.valdiar()) {
        return;
      }
      if (this.editedIndex > -1) {
        //editar
        axios
          .put(
            "item/update",
            {
              _id: me._id,
              name: me.name,
              code: me.code,
              stock: me.stock,
              category: me.categoria,
              sale_price: me.sale_price,
              description: me.description,
            },
            config
          )
          .then((response) => {
            me.limpiar();
            me.close();
            me.listar();
          })
          .catch((e) => {
            console.log(e);
          });
      } else {
        //guardar
        axios
          .post(
            "item/add",
            {
              name: this.name,
              code: this.code,
              stock: this.stock,
              category: this.categoria,
              sale_price: this.sale_price,
              description: this.description,
            },
            config
          )
          .then((response) => {
            me.limpiar();
            me.close();
            me.listar();
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },

    editItem(item) {
      this._id = item._id;
      this.name = item.name;
      (this.code = item.code),
        (this.stock = item.stock),
        (this.sale_price = item.sale_price),
        (this.categoria = item.category._id),
        (this.description = item.description);
      this.dialog = true;
      this.editedIndex = 1;
    },

    estadoCategoria(accion, item) {
      this.addModal = 1;
      this.addName = item.name;
      this.add_id = item._id;
      if (accion === 1) {
        //ok activar
        this.addAction = 1;
      } else if (accion === 2) {
        //ok desactivar
        this.addAction = 2;
      } else {
        this.addAction = 0;
      }
    },
    activar() {
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .put(
          "item/activate",
          {
            _id: this.add_id,
          },
          config
        )
        .then((response) => {
          this.addModal = 0;
          this.addAction = 0;
          this.addName = "";
          this.add_id = "";
          this.listar();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    desactivar() {
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .put(
          "item/deactivate",
          {
            _id: this.add_id,
          },
          config
        )
        .then((response) => {
          this.addModal = 0;
          this.addAction = 0;
          this.addName = "";
          this.add_id = "";
          this.listar();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    close() {
      this.dialog = false;
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.categorias[this.editedIndex], this.editedItem);
      } else {
        this.categorias.push(this.editedItem);
      }
      this.close();
    },
  },
};
</script>
