<template>
  <v-layout align-center justify-center>
    <v-flex xs12 sm8 md6 lg5 x14>
      <v-card>
        <v-toolbar dark color="blue darken-3">
          <v-toolbar-title>
            Acceso al sistema
          </v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-text-field
            v-model="email"
            autofocus
            color="accent"
            label="Email"
            required
          >
          </v-text-field>
          <v-text-field
            v-model="password"
            type="password"
            color="accent"
            label="Password"
            required
          >
          </v-text-field>
          <v-flex v-if="errorLogin" class="red--text">
            {{ errorLogin }}
          </v-flex>
        </v-card-text>
        <v-card-actions class="px-3 pb-3">
          <v-flex text-xs-right>
            <v-btn @click="ingresar()" color="primary">Ingresar</v-btn>
          </v-flex>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      email: "",
      password: "",
      errorLogin: "",
    };
  },
  methods: {
    ingresar() {
      axios
        .post("user/login", { email: this.email, password: this.password })
        .then((response) => {
          this.limpiar();
          return response.data;
        })
        .then((data) => {
          this.$store.dispatch("guardarToken", data.user_token);
          this.$routes.push({ name: "home" });
          this.limpiar();
        })
        .catch((e) => {
          if (e.response.status == 404) {
            this.errorLogin =
              "El usuario no existe o las credenciales no son las correctas, verificar datos por fas!";
          } else {
            this.errorLogin =
              "Error en el servidor, contactar a soporte tecnico!";
          }
        });
    },
    limpiar() {
      this.email = "";
      this.password = "";
      this.errorLogin = "";
    },
  },
};
</script>
