import Vue from "vue";
import Router from "vue-router";
import store from "./store.js";
import Home from "./views/Home.vue";
import Categoria from "./components/Categoria.vue";
import Login from "./components/Login.vue";
import User from "./components/Usuarios.vue";
import Articulo from "./components/Articulo.vue";
import Cliente from "./components/Cliente.vue";
import Proveedor from "./components/Proveedor.vue";
import Ingreso from "./components/Ingreso.vue";
import Venta from "./components/Venta.vue";

Vue.use(Router);

let routes = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home,
      meta: {
        Admin: true,
        Manager: true,
        Seller: true,
      },
    },
    {
      path: "/login",
      name: "login",
      component: Login,
      meta: {
        libre: true,
      },
    },
    {
      path: "/categoria",
      name: "categoria",
      component: Categoria,
      meta: {
        Admin: true,
        Manager: true,
      },
    },
    {
      path: "/articulo",
      name: "articulo",
      component: Articulo,
      meta: {
        Admin: true,
        Manager: true,
      },
    },
    {
      path: "/ingreso",
      name: "ingreso",
      component: Ingreso,
      meta: {
        Admin: true,
        Manager: true,
      },
    },
    {
      path: "/proveedor",
      name: "proveedor",
      component: Proveedor,
      meta: {
        Admin: true,
        Manager: true,
      },
    },
    {
      path: "/cliente",
      name: "cliente",
      component: Cliente,
      meta: {
        Admin: true,
        Seller: true,
      },
    },
    {
      path: "/venta",
      name: "venta",
      component: Venta,
      meta: {
        Admin: true,
        Seller: true,
      },
    },
    {
      path: "/usuarios",
      name: "usuarios",
      component: User,
      meta: {
        Admin: true,
      },
    },
  ],
});

routes.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.libre)) {
    next();
  } else if (store.state.user && store.state.user.rol == "Admin") {
    if (to.matched.some((record) => record.meta.Admin)) {
      next();
    }
  } else if (store.state.user && store.state.user.rol == "Seller") {
    if (to.matched.some((record) => record.meta.Seller)) {
      next();
    }
  } else if (store.state.user && store.state.user.rol == "Manager") {
    if (to.matched.some((record) => record.meta.Manager)) {
      next();
    }
  } else {
    next({ name: "login" });
  }
});
export default routes;
