<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar flat color="white">
        <v-toolbar-title>Clientes</v-toolbar-title>
        <v-divider class="mx-2" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-text-field
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Búsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>

        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">Nuevo</v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm12 md12>
                    <v-text-field v-model="name" label="Name"></v-text-field>
                  </v-flex>

                  <v-flex xs12 sm6 md6>
                    <v-select
                      v-model="document_type"
                      :items="document_types"
                      label="Tipo Documento"
                    ></v-select>
                  </v-flex>
                  <v-flex xs12 sm6 md6>
                    <v-text-field
                      v-model="document_number"
                      label="Número Documento"
                    ></v-text-field>
                  </v-flex>

                  <v-flex xs12 sm8 md8>
                    <v-text-field v-model="email" label="Email"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm4 md4>
                    <v-text-field
                      v-model="phone"
                      label="Telefono"
                    ></v-text-field>
                  </v-flex>

                  <v-flex xs12 sm12 md12 v-if="valida">
                    <div
                      class="red--text"
                      v-for="v in validaMensaje"
                      :key="v"
                      v-text="v"
                    ></div>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="close">Cancelar</v-btn>
              <v-btn color="blue darken-1" flat @click="guardar">Guardar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <v-dialog v-model="addModal" max-width="500px">
          <v-card>
            <v-card-title>
              <span class="headline">{{ titoloModal }}</span>
            </v-card-title>

            <v-card-text>
              <span class="orange--text"> {{ cuerpoModal }}</span>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="addModal = 0"
                >Cancelar</v-btn
              >
              <v-btn
                color="green darken-1"
                flat
                @click="activar()"
                v-if="addAction == 1"
                >Activar</v-btn
              >
              <v-btn
                color="red darken-1 "
                flat
                @click="desactivar()"
                v-if="addAction == 2"
                >Desactivar</v-btn
              >
              <v-btn
                color="red darken-1 "
                flat
                @click="eliminar()"
                v-if="addAction == 3"
                >Eliminar</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
      <v-data-table
        :headers="headers"
        :items="personas"
        :search="search"
        class="elevation-1"
      >
        <template v-slot:items="props">
          <td>{{ props.item.person_type }}</td>
          <td>{{ props.item.name }}</td>
          <td>{{ props.item.document_type }}</td>
          <td>{{ props.item.email }}</td>
          <td>{{ props.item.phone }}</td>
          <td>{{ props.item.status }}</td>
          <td>
            <div v-if="props.item.status">
              <span class="blue--text">Activo</span>
            </div>
            <div v-else>
              <span class="red--text">Inactivo</span>
            </div>
          </td>
          <td class="justify-center layout px-0">
            <v-icon small class="mr-2" @click="editItem(props.item)">
              edit
            </v-icon>
            <template v-if="props.item.status">
              <v-icon
                medium
                color="red"
                @click="estadoCategoria(2, props.item)"
              >
                block
              </v-icon>
            </template>
            <template v-else>
              <v-icon
                medium
                color="green"
                @click="estadoCategoria(1, props.item)"
              >
                check
              </v-icon>
            </template>
            <template>
              <v-icon
                medium
                color="red"
                @click="estadoCategoria(3, props.item)"
              >
                delete
              </v-icon>
            </template>
          </td>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary" @click="listar()">Reset</v-btn>
        </template>
      </v-data-table>
    </v-flex>
  </v-layout>
</template>
<script>
import axios from "axios";

export default {
  data() {
    return {
      dialog: false,
      search: "",
      personas: [],
      titoloModal: "",
      cuerpoModal: "",
      headers: [
        { text: "Tipo de Persona", value: "person_type", sortable: false },
        {
          text: "Nombre",
          align: "left",
          sortable: true,
          value: "name",
        },
        { text: "Tipo Documento", value: "document_type", sortable: false },
        { text: "Identificación", value: "document_number", sortable: false },
        { text: "Email", value: "email", sortable: false },
        { text: "Telefono", value: "phone", sortable: false },

        { text: "Estado", value: "status", sortable: false },
        { text: "Acciones", value: "name", sortable: false },
      ],
      editedIndex: -1,
      _id: "",
      name: "",
      tipo_perosna: "Customer",
      document_types: ["Física", "Juridica", "DIMEX", "NITE", "Otra"],
      document_type: "",
      document_number: "",
      email: "",
      phone: "",
      status: "",

      valida: 0,
      validaMensaje: [],
      addModal: 0,
      addAction: 0,
      addName: "",
      add_id: "",
    };
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nuevo Registro" : "Editar Registro";
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
  },
  created() {
    this.listar();
  },
  methods: {
    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .get("person/list/customer", config)
        .then((response) => {
          me.personas = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    limpiar() {
      this._id = "";
      this.rol = "";
      this.name = "";
      this.document_type = "";
      this.document_number = "";
      this.email = "";
      this.phone = "";
      this.valida = 0;
      this.validaMensaje = [];
      this.editedIndex = -1;
    },
    valdiar() {
      this.valida = 0;
      this.validaMensaje = [];
      if (this.name.length < 1 || this.name.length > 50) {
        this.validaMensaje.push(
          "El nombre debe de ser entre 1 a 50 caracteres."
        );
      }
      if (this.document_number.length > 50) {
        this.validaMensaje.push(
          "El numero de documento debe de ser menor a 50 caracteres."
        );
      }
      if (this.email.length < 0) {
        this.validaMensaje.push("El email es requerido.");
      }

      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    guardar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      if (this.valdiar()) {
        return;
      }
      if (this.editedIndex > -1) {
        //editar
        axios
          .put(
            "person/update",
            {
              _id: me._id,
              person_type: this.tipo_perosna,
              name: this.name,
              document_type: this.document_type,
              document_number: this.document_number,
              email: this.email,
              phone: this.phone,
            },
            config
          )
          .then((response) => {
            me.close();
            me.listar();
          })
          .catch((e) => {
            console.log(e);
          });
      } else {
        //guardar
        axios
          .post(
            "person/add",
            {
              person_type: this.tipo_perosna,
              name: this.name,
              document_type: this.document_type,
              document_number: this.document_number,
              email: this.email,
              phone: this.phone,
            },
            config
          )
          .then((response) => {
            me.close();
            me.listar();
          })
          .catch((e) => {
            console.log(e);
          });
      }
    },

    editItem(item) {
      this._id = item._id;

      this.rol = item.rol;
      this.name = item.name;
      this.document_type = item.document_type;
      this.document_number = item.document_number;
      this.email = item.email;
      this.password = item.password;
      this.phone = item.phone;
      this.dialog = true;
      this.editedIndex = 1;
    },

    estadoCategoria(accion, item) {
      this.addModal = 1;
      this.addName = item.name;
      this.add_id = item._id;
      if (accion === 1) {
        //ok activar
        this.addAction = 1;
        this.titoloModal = "Activar la persona";
        this.cuerpoModal = "Va ha activar a la persona " + this.addName;
      } else if (accion === 2) {
        //ok desactivar
        this.titoloModal = "Desactivar la persona";
        this.cuerpoModal = "Va ha desactivar a la persona " + this.addName;
        this.addAction = 2;
      } else if (accion === 3) {
        //ok eliminar
        this.titoloModal = "Eliminar la persona";
        this.cuerpoModal = "Va a eliminar a la persona " + this.addName;
        this.addAction = 3;
      } else {
        this.titoloModal = "Error";
        this.cuerpoModal =
          "ups algo paso en la acción del la persona " + this.addName;
        this.addAction = 0;
      }
    },
    activar() {
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .put(
          "person/activate",
          {
            _id: this.add_id,
          },
          config
        )
        .then((response) => {
          this.addModal = 0;
          this.addAction = 0;
          this.addName = "";
          this.add_id = "";
          this.listar();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    desactivar() {
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .put(
          "person/deactivate",
          {
            _id: this.add_id,
          },
          config
        )
        .then((response) => {
          this.addModal = 0;
          this.addAction = 0;
          this.addName = "";
          this.add_id = "";
          this.listar();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    eliminar() {
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .put(
          "user/remove",
          {
            _id: this.add_id,
          },
          config
        )
        .then((response) => {
          this.addModal = 0;
          this.addAction = 0;
          this.addName = "";
          this.add_id = "";
          this.listar();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    close() {
      this.limpiar();
      this.dialog = false;
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.usuarios[this.editedIndex], this.editedItem);
      } else {
        this.usuarios.push(this.editedItem);
      }
      this.close();
    },
  },
};
</script>
