<template>
  <v-layout align-start>
    <v-flex>
      <v-toolbar flat color="white">
        <v-toolbar-title>Ventas</v-toolbar-title>
        <v-divider class="mx-2" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-text-field
          v-if="!verNuevo"
          class="text-xs-center"
          v-model="search"
          append-icon="search"
          label="Búsqueda"
          single-line
          hide-details
        ></v-text-field>
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          v-if="!verNuevo"
          dark
          class="mb-2"
          @click="mostrarNuevo()"
          >Nuevo</v-btn
        >
        <v-dialog v-model="dialog" max-width="1000px">
          <v-card>
            <v-card-title>
              <span class="headline">Selecione un artículo</span>
            </v-card-title>

            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm12 md12 lg12 xl12>
                    <v-text-field
                      v-model="texto"
                      @keyup.enter="listarArticulos()"
                      class="text-xs-center"
                      append-icon="search"
                      label="Búsqueda"
                    ></v-text-field>
                    <template>
                      <v-data-table
                        :headers="cabeceraArticulos"
                        :items="articulos"
                        class="elevation-1"
                      >
                        <template v-slot:items="props">
                          <td>{{ props.item.code }}</td>
                          <td>{{ props.item.name }}</td>
                          <td>{{ props.item.category.name }}</td>
                          <td>{{ props.item.stock }}</td>
                          <td>{{ props.item.sale_price }}</td>
                          <td>{{ props.item.description }}</td>
                          <td class="justify-center layout px-0">
                            <v-icon
                              small
                              class="mr-2"
                              @click="agregarDetalles(props.item)"
                            >
                              add
                            </v-icon>
                          </td>
                        </template>
                      </v-data-table>
                    </template>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="close">Cancelar</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <v-dialog v-model="addModal" max-width="500px">
          <v-card>
            <v-card-title>
              <span class="headline">{{ titoloModal }}</span>
            </v-card-title>

            <v-card-text>
              <span class="orange--text"> {{ cuerpoModal }}</span>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="addModal = 0"
                >Cancelar</v-btn
              >
              <v-btn
                color="green darken-1"
                flat
                @click="activar()"
                v-if="addAction == 1"
                >Activar</v-btn
              >
              <v-btn
                color="red darken-1 "
                flat
                @click="desactivar()"
                v-if="addAction == 2"
                >Desactivar</v-btn
              >
              <v-btn
                color="red darken-1 "
                flat
                @click="eliminar()"
                v-if="addAction == 3"
                >Eliminar</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="comprobanteModal" max-width="1000px">
          <v-card>
            <v-card-title class="headline">
              <v-btn @click="crearPDF()"> <v-icon>print</v-icon></v-btn
              >Reporte de venta
            </v-card-title>
            <v-card-text>
              <div id="factura">
                <header>
                  <div id="logo">
                    <img
                      id="imagen"
                      src="data:image/png;base64,"
                    />
                  </div>
                  <div id="datos">
                    <p id="encabezado">
                      <b>IncanatoIT</b><br />José Gálvez 1368, Chongoyape -
                      Chiclayo, Perú<br />Telefono:(+51)931742904<br />Email:jcarlos.ad7@gmail.com
                    </p>
                  </div>
                  <div id="fact">
                    <p>
                      {{ register_type }}<br />
                      {{ register_serie }}--{{ register_number }}<br />
                      {{ fecha }}
                    </p>
                  </div>
                </header>
                <br />
                <section>
                  <div>
                    <table id="facliente">
                      <tbody>
                        <tr>
                          <td id="cliente">
                            <strong>Sr(a). {{ persona.name }}</strong
                            ><br />
                            <strong>Documento:</strong>
                            {{ persona.document_number }}<br />
                            <strong>Dirección:</strong> Zarumilla 113 -
                            Chiclayo<br />
                            <strong>Teléfono:</strong> {{ persona.phone }}<br />
                            <strong>Email:</strong> {{ persona.email }}
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </section>
                <br />
                <section>
                  <div>
                    <table id="facarticulo">
                      <thead>
                        <tr id="fa">
                          <th>CANT</th>
                          <th>DESCRIPCION</th>
                          <th>PRECIO UNIT</th>
                          <th>DESC.</th>
                          <th>PRECIO TOTAL</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="det in detalles" :key="det._id">
                          <td style="text-align:center;">{{ det.quantity }}</td>
                          <td>{{ det.name }}</td>
                          <td style="text-align:right;">
                            {{ det.sale_price }}
                          </td>
                          <td style="text-align:right;">{{ det.discount }}</td>
                          <td style="text-align:right;">
                            {{ det.quantity * det.sale_price - det.discount }}
                          </td>
                        </tr>
                      </tbody>
                      <tfoot>
                        <tr>
                          <th></th>
                          <th></th>
                          <th></th>
                          <th style="text-align:right;">SUBTOTAL</th>
                          <th style="text-align:right;">
                            ${{
                              (totalParcial = (total - totalImpuesto).toFixed(
                                2
                              ))
                            }}
                          </th>
                        </tr>
                        <tr>
                          <th></th>
                          <th></th>
                          <th></th>
                          <th style="text-align:right;">
                            IVA({{ impuesto }}%)
                          </th>
                          <th style="text-align:right;">
                            ${{
                              (totalImpuesto = (
                                (total * impuesto) /
                                (1 + impuesto)
                              ).toFixed(2))
                            }}
                          </th>
                        </tr>
                        <tr>
                          <th></th>
                          <th></th>
                          <th></th>
                          <th style="text-align:right;">TOTAL</th>
                          <th style="text-align:right;">
                            $ {{ (total = calcularTotal) }}
                          </th>
                        </tr>
                      </tfoot>
                    </table>
                  </div>
                </section>
                <br />
                <br />
                <footer>
                  <div id="gracias">
                    <p><b>Gracias por su compra!</b></p>
                  </div>
                </footer>
              </div>
            </v-card-text>

            <v-card-actions>
              <v-btn color="blue darken-1" flat @click="ocultarComprobante()"
                >Cancelar</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
      <v-data-table
        :headers="headers"
        :items="ventas"
        :search="search"
        class="elevation-1"
        v-if="!verNuevo"
      >
        <template v-slot:items="props">
          <td>{{ props.item.user.name }}</td>
          <td>{{ props.item.people.name }}</td>
          <td>{{ props.item.register_type }}</td>
          <td>{{ props.item.register_serie }}</td>
          <td>{{ props.item.register_number }}</td>
          <td>{{ props.item.tax }}</td>
          <td>{{ props.item.total }}</td>
          <td>
            <div v-if="props.item.status">
              <span class="blue--text">Aceptado</span>
            </div>
            <div v-else>
              <span class="red--text">Anulada</span>
            </div>
          </td>
          <td>{{ props.item.createdAt }}</td>
          <td class="justify-center layout px-0">
            <v-icon small class="mr-2" @click="verIngreso(props.item)">
              tab
            </v-icon>
            <v-icon small class="mr-2" @click="mostrarComprobante(props.item)">
              print
            </v-icon>
            <template v-if="props.item.status">
              <v-icon
                medium
                color="red"
                @click="estadoCategoria(2, props.item)"
              >
                block
              </v-icon>
            </template>
            <template v-else>
              <v-icon
                medium
                color="green"
                @click="estadoCategoria(1, props.item)"
              >
                check
              </v-icon>
            </template>
          </td>
        </template>
        <template v-slot:no-data>
          <v-btn color="primary" @click="listar()">Reset</v-btn>
        </template>
      </v-data-table>
      <v-container grid-list-sm class="pa-4 white" v-if="verNuevo">
        <v-layout row wrap>
          <v-flex xs12 sm4 md4 lg4 xl14>
            <v-select
              v-model="register_type"
              :items="comprobantes"
              label="Tipo de Comprobante"
            ></v-select>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl14>
            <v-text-field
              v-model="register_serie"
              label="Serie Comprobante"
            ></v-text-field>
          </v-flex>
          <v-flex xs12 sm4 md4 lg4 xl14>
            <v-text-field
              v-model="register_number"
              label="Número de Comprobante"
            ></v-text-field>
          </v-flex>

          <v-flex xs12 sm8 md8 lg8 xl18>
            <v-autocomplete :items="personas" v-model="persona" label="Cliente">
            </v-autocomplete>
          </v-flex>

          <v-flex xs12 sm4 md4 lg4 xl18>
            <v-text-field
              type="number"
              v-model="impuesto"
              label="Impuesto"
            ></v-text-field>
          </v-flex>
          <v-flex xs12 sm8 md8 lg8 xl8>
            <v-text-field
              v-model="codigo"
              label="Código"
              @keyup.enter="buscarCodigo()"
            ></v-text-field>
          </v-flex>
          <v-flex xs12 sm2 md2 lg2 xl12>
            <v-btn small fab dark color="teal" @click="mostrarModalArticulos()">
              <v-icon dark>list</v-icon>
            </v-btn>
          </v-flex>
          <v-flex xs12 sm2 md2 lg2 xl12 v-show="errorArticulo">
            <div class="red--text" v-text="errorArticulo"></div>
          </v-flex>
          <v-flex xs12 sm12 md2 lg12 xl12>
            <v-data-table
              :headers="cabeceraDetalles"
              :items="detalles"
              class="elevation-1"
            >
              <template v-slot:items="props">
                <td>
                  <template>
                    <v-icon
                      medium
                      color="red"
                      @click="eliminarDetalle(detalles, props.item)"
                    >
                      delete
                    </v-icon>
                  </template>
                </td>
                <td class="text-xs-center">{{ props.item.name }}</td>
                <td class="text-xs-center">
                  <v-text-field
                    type="number"
                    v-model="props.item.quantity"
                  ></v-text-field>
                </td>
                <td class="text-xs-center">
                  <v-text-field
                    type="number"
                    v-model="props.item.sale_price"
                  ></v-text-field>
                </td>
                <td class="text-xs-center">
                  <v-text-field
                    type="number"
                    v-model="props.item.discount"
                  ></v-text-field>
                </td>
                <td class="text-xs-center">
                  ${{
                    props.item.quantity * props.item.sale_price -
                      props.item.discount
                  }}
                </td>
              </template>
              <template v-slot:no-data>
                <h3>No hay articulos agregados al detalle</h3>
              </template>
            </v-data-table>
            <v-flex class="text-xs-right">
              <strong>Total Parcial:</strong> ${{
                (totalParcial = (total - totalImpuesto).toFixed(2))
              }}
            </v-flex>
            <v-flex class="text-xs-right">
              <strong>Total Impuesto:</strong> ${{
                (totalImpuesto = ((total * impuesto) / (1 + impuesto)).toFixed(
                  2
                ))
              }}
            </v-flex>
            <v-flex class="text-xs-right">
              <strong>Total Neto:</strong> ${{ (total = calcularTotal) }}
            </v-flex>
          </v-flex>
          <v-flex xs12 sm12 md12 v-if="valida">
            <div
              class="red--text"
              v-for="v in validaMensaje"
              :key="v"
              v-text="v"
            ></div>
          </v-flex>
          <v-flex xs12 sm12 md12 lg2 xl12>
            <v-card-actions>
              <v-btn color="blue darken-1" @click.native="ocultarNuevo()"
                >Cancelar</v-btn
              >
              <v-btn
                color="success"
                v-if="!verDetalle"
                @click.native="guardar()"
                >Guardar</v-btn
              >
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-flex>
  </v-layout>
</template>
<script>
import axios from "axios";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";

export default {
  data() {
    return {
      dialog: false,
      search: "",
      ventas: [],
      titoloModal: "",
      cuerpoModal: "",
      headers: [
        {
          text: "Usuario",
          align: "left",
          sortable: true,
          value: "user.name",
        },
        { text: "Cliente", value: "people.name", sortable: true },
        { text: "Tipo de Comprobante", value: "register_type", sortable: true },
        { text: "Serie Comprobante", value: "register_serie", sortable: true },
        {
          text: "Número de Registro",
          value: "register_number",
          sortable: true,
        },
        { text: "Impuesto", value: "tax", sortable: false },
        { text: "Total", value: "total", sortable: false },
        { text: "Estado", value: "status", sortable: false },
        { text: "Creado", value: "createdAt", sortable: false },
        { text: "Acciones", value: "name", sortable: false },
      ],
      _id: "",
      persona: "",
      personas: [],
      register_type: "",
      comprobantes: ["Boleta", "Factura", "Ticket", "Guia"],
      document_types: ["Física", "Juridica", "DIMEX", "NITE", "Otra"],
      register_serie: "",
      register_number: "",
      impuesto: 0.13,
      status: "",
      codigo: "",
      cabeceraDetalles: [
        { text: "Borrar", value: "borrar", sortable: false },
        { text: "Artículo", value: "articulo", sortable: false },
        { text: "Cantidad", value: "cantidad", sortable: false },
        { text: "Precio", value: "precio", sortable: false },
        { text: "Descuento", value: "descuento", sortable: false },
        { text: "Sub Total", value: "subtotal", sortable: false },
      ],
      detalles: [],
      verNuevo: 0,
      errorArticulo: null,
      total: 0,
      totalParcial: 0,
      totalImpuesto: 0,
      articulos: [],
      texto: "",
      cabeceraArticulos: [
        {
          text: "Código",
          align: "left",
          sortable: true,
          value: "code",
        },
        { text: "Nombre", value: "name", sortable: true },
        { text: "Categoría", value: "category.name", sortable: true },
        { text: "Stock", value: "stock", sortable: true },
        { text: "Precio", value: "precio", sortable: false },
        { text: "Descripcion", value: "descripcion", sortable: false },
        { text: "Selecionar", value: "name", sortable: false },
      ],
      verDetalle: 0,
      valida: 0,
      validaMensaje: [],
      addModal: 0,
      addAction: 0,
      addName: "",
      add_id: "",
      comprobanteModal: 0,
      fecha: "",
    };
  },
  computed: {
    calcularTotal: function() {
      let resultado = 0.0;
      for (let i = 0; i < this.detalles.length; i++) {
        resultado =
          resultado +
          (this.detalles[i].quantity * this.detalles[i].sale_price -
            this.detalles[i].discount);
      }
      return resultado;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
  },
  created() {
    this.listar();
    this.selectPerson();
  },
  methods: {
    crearPDF() {
      var quotes = document.getElementById("factura");
      html2canvas(quotes).then(function(canvas) {
        var imgData = canvas.toDataURL("image/png");
        var imgWidth = 210;
        var pageHeight = 295;

        var imgHeight = (canvas.height * imgWidth) / canvas.width;
        var heightLeft = imgHeight;

        var doc = new jsPDF("p", "mm", "a4");
        var position = 0;

        doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          doc.addPage();
          doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
          heightLeft -= pageHeight;
        }
        doc.save("ComprobanteVenta.pdf");
      });
    },
    mostrarComprobante(item) {
      this.limpiar();
      this.register_type = item.register_type;
      this.register_serie = item.register_serie;
      this.register_number = item.register_number;
      this.fecha = item.createdAt;
      this.persona = item.people;
      this.impuesto = item.tax;
      this.listarDetalles(item._id);
      this.comprobanteModal = 1;
    },
    ocultarComprobante() {
      this.comprobanteModal = 0;
    },
    selectPerson() {
      let me = this;
      let personaArray = [];
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .get("person/list/customer", config)
        .then((response) => {
          personaArray = response.data;
          personaArray.map(function(x) {
            me.personas.push({ text: x.name, value: x._id });
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },
    buscarCodigo() {
      let me = this;
      me.errorArticulo = null;
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .get("item/query/code?code=" + me.codigo, config)
        .then((response) => {
          me.agregarDetalles(response.data);
        })
        .catch((e) => {
          me.errorArticulo = "No existe el articulo";
        });
    },
    agregarDetalles(data) {
      this.errorArticulo = null;
      if (this.encuentra(data._id)) {
        this.errorArticulo = "El artículo ha sido agregado";
      } else {
        this.detalles.push({
          _id: data._id,
          name: data.name,
          quantity: 1,
          sale_price: data.sale_price,
          discount: 0,
        });
        this.codigo = "";
      }
    },
    encuentra(id) {
      let sw = 0;
      for (let i = 0; i < this.detalles.length; i++) {
        if (this.detalles[i]._id == id) {
          sw = true;
        }
      }
      return sw;
    },
    eliminarDetalle(data, item) {
      let i = data.indexOf(item);
      if (i != -1) {
        data.splice(i, 1); //delte just index
      }
    },
    listarArticulos() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .get("item/list?condition=" + this.texto, config)
        .then((response) => {
          me.articulos = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    listarDetalles(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .get("sell/query?_id=" + id, config)
        .then((response) => {
          me.detalles = response.data.detail;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    verIngreso(item) {
      this.limpiar();
      this.register_type = item.register_type;
      this.register_serie = item.register_serie;
      this.register_number = item.register_number;
      this.persona = item.people._id;
      this.impuesto = item.tax;
      this.listarDetalles(item._id);
      this.verNuevo = 1;
      this.verDetalle = 1;
    },
    mostrarModalArticulos() {
      this.dialog = 1;
    },
    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .get("sell/list", config)
        .then((response) => {
          me.ventas = response.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    limpiar() {
      this._id = "";
      this.persona = "";
      this.register_type = "";
      this.register_serie = "";
      this.register_number = "";
      this.document_number = "";
      this.tax = 0.13;
      this.total = 0;
      this.detail = [];
      this.verNuevo = 0;
      this.valida = 0;
      this.validaMensaje = [];
      this.verDetalle = 0;
    },
    valdiar() {
      this.valida = 0;
      this.validaMensaje = [];
      if (!this.persona) {
        this.validaMensaje.push("Debe selecionar un cliente.");
      }
      if (!this.register_type) {
        this.validaMensaje.push("Debe selecionar un tipo de registro.");
      }
      if (!this.register_serie) {
        this.validaMensaje.push("Debe selecionar un tipo de registro.");
      }
      if (!this.register_number) {
        this.validaMensaje.push("Debe selecionar un tipo de registro.");
      }
      if (this.impuesto < 0) {
        console.log(this.impuesto);
        this.validaMensaje.push("El impuesto debe ser valido.");
      }
      if (this.detalles.length <= 0) {
        this.validaMensaje.push("Ingrese al meno un artículo al detalle.");
      }

      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    mostrarNuevo() {
      this.verNuevo = 1;
    },
    ocultarNuevo() {
      this.verNuevo = 0;
      this.limpiar();
    },
    guardar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      if (this.valdiar()) {
        return;
      }

      //guardar
      axios
        .post(
          "sell/add",
          {
            user: this.$store.state.user._id,
            people: this.persona,
            register_type: this.register_type,
            register_serie: this.register_serie,
            document_number: this.document_number,
            register_number: this.register_number,
            tax: this.impuesto,
            total: this.total,
            detail: this.detalles,
          },
          config
        )
        .then((response) => {
          me.close();
          me.listar();
        })
        .catch((e) => {
          console.log(e);
        });
    },

    estadoCategoria(accion, item) {
      this.addModal = 1;
      this.addName = item.register_number;
      this.add_id = item._id;
      if (accion === 1) {
        //ok activar
        this.addAction = 1;
        this.titoloModal = "Activar Usuario";
        this.cuerpoModal = "Va ha activar al usuario " + this.addName;
      } else if (accion === 2) {
        //ok desactivar
        this.titoloModal = "Desactivar Usuario";
        this.cuerpoModal = "Va ha desactivar al usuario " + this.addName;
        this.addAction = 2;
      } else if (accion === 3) {
        //ok eliminar
        this.titoloModal = "Eliminar Usuario";
        this.cuerpoModal = "Va a eliminar al usuario " + this.addName;
        this.addAction = 3;
      } else {
        this.titoloModal = "Error";
        this.cuerpoModal =
          "ups algo paso en la acción del usuario " + this.addName;
        this.addAction = 0;
      }
    },
    activar() {
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .put(
          "sell/activate",
          {
            _id: this.add_id,
          },
          config
        )
        .then((response) => {
          this.addModal = 0;
          this.addAction = 0;
          this.addName = "";
          this.add_id = "";
          this.listar();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    desactivar() {
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .put(
          "sell/deactivate",
          {
            _id: this.add_id,
          },
          config
        )
        .then((response) => {
          this.addModal = 0;
          this.addAction = 0;
          this.addName = "";
          this.add_id = "";
          this.listar();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    eliminar() {
      let header = { Token: this.$store.state.token };
      let config = { headers: header };
      axios
        .put(
          "user/remove",
          {
            _id: this.add_id,
          },
          config
        )
        .then((response) => {
          this.addModal = 0;
          this.addAction = 0;
          this.addName = "";
          this.add_id = "";
          this.listar();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    close() {
      this.limpiar();
      this.dialog = false;
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.usuarios[this.editedIndex], this.editedItem);
      } else {
        this.usuarios.push(this.editedItem);
      }
      this.close();
    },
  },
};
</script>
